@mixin divider($color, $padding) {
  display: block;
  text-align: center;
  overflow: hidden;
  white-space: nowrap;

  span {
    position: relative;
    display: inline-block;

    &:before, &:after {
      content: "";
      position: absolute;
      top: 50%;
      width: 9999px;
      height: 1px;
      background: $color;
    }

    &:before {
      right: 100%;
      margin-right: $padding;
    }

    &:after {
      left: 100%;
      margin-left: $padding;
    }
  }
}
